.wrapper{
    width: 100%;
    height: 100vh;
    .login-box{
        width: 360px;
        margin: 7% auto;
        .login-logo{
            font-size: 35px;
            text-align: center;
            margin-bottom: 25px;
            font-weight: 300;
        }
        .card{
        box-shadow: 0 0 1px rgba(0,0,0,.125), 0 1px 3px rgba(0,0,0,.2);
        .car-body, .login-card-body{
            flex: 1 1 auto;
            padding: 1.25rem;
            .login-box-msg{
                margin:0;
                text-align: center;
                padding: 0 20px 20px 20px;;
            }
            .alert-danger{
                color: #ffffff;
                border-color: #d32535;
                background-color: #d32535;
                padding: 0.75rem 1.25rem;
                position: relative;
                margin-bottom: 1rem;
                text-align: center;
                font-weight: 700;
                font-size: 18px;
            }
        }
        }
    }
}